@import "include-media";

header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 500;
	background-color: rgba(0, 0, 0, 0.8);

	&.sticky {
		.container {
			height: 80px;
		}
	}

	.container {
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 110px;
		transition-duration: 500ms;

	}

	.brands {
		display: flex;
		align-items: center;
		column-gap: 20px;
	}

	nav {
		background-color: #1a265c;
		position: absolute;
		top: 100%;
		left: -100%;
		width: 100%;
		height: calc(100vh - 84px);

		transition: 0.5s;
		overflow: hidden;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			height: 100%;
			left: 0;
			right: 0;
			background-color: rgba($color: black, $alpha: 0.4);
			transform-origin: 0 0;
			transform: skew(-14deg) translateX(-120%);
			transition: all 0.275s 0.1s;
		}

		&.show {
			left: 0;
			z-index: 100;

			&::before {
				transform: skew(-14deg) translateX(0);
			}

			a {
				opacity: 1;
				transform: translateY(0);
			}
		}

		ul {
			transform: translateX(-18%) skew(-14deg);
			padding: 42px;
			display: flex;
			column-gap: 42px;
			flex-direction: column;
			text-align: left;
			row-gap: 24px;
			padding-right: 10px;

			li {
				display: block;
				margin: 0.5rem 0;
				text-align: right;
				transform: skew(14deg);

				&:nth-child(1) a {
					transition: all 275ms 250ms
				}

				&:nth-child(2) a {
					transition: all 275ms 350ms
				}

				&:nth-child(3) a {
					transition: all 275ms 450ms;
				}

				&:nth-child(3) a {
					transition: all 275ms 550ms;
				}

				a {
					opacity: 0;
					transform: translateY(-10px);
				}
			}
		}

		.link {
			color: white;
			font-weight: bold;
			font-size: 1.7rem;
		}
	}

	@include media(">tablet") {
		nav {
			position: static;
			height: auto;
			display: block;
			flex-grow: 1;
			background-color: transparent;

			&::before {
				display: none;
			}

			ul {
				justify-content: flex-end;
				flex-direction: row;
				padding: 0;
				transform: unset;

				li {
					transform: unset;

					a {
						opacity: 1;
					}
				}
			}

			.link {
				color: #ffffff;
			}
		}
	}
}