@import "include-media";

.slide {
	display: flex;
	align-items: center;
	padding-left: 50px;
	padding-right: 50px;
	width: 100%;
	height: 700px;
	background-blend-mode: overlay;
	background-color: rgba($color: black, $alpha: 0.3);
	background-repeat: no-repeat;
	background-size: contain;
	background-position: center 10%;
}

.wrapper_link {
	display: block;
	height: 100%;
	width: 100%;
}