@import "include-media";

.container {
	width: 100%;
	max-width: 1200px;
	margin: 0 auto;
	padding: 0 24px;
	text-align: left;

	@include media(">=tablet") {
		padding: 0 36px;
	}

	&::after {
		display: none;
	}

	&::before {
		display: none;
	}
}

.link {
	color: var(--color-primary);
	font-size: 1.6rem;
	font-weight: bold;
	line-height: 1.5;
	border-bottom: 2px solid var(--color-primary);
	white-space: nowrap;

	&.nav {
		text-decoration: none;
		border-bottom: none;
	}

	&.light {
		color: #ffffff;
		border-bottom-color: #ffffff;
	}

	&.green {
		color: rgb(159, 215, 0);
		border-bottom-color: rgb(159, 215, 0);
	}
}

.space-between-vertical {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.row-flex {
	
	&.reverse {
		flex-direction: row-reverse;
	}

	.col-md-6 {
		flex-basis: calc((100% - 24px) / 2);
	}

	&.nowrap {
		flex-wrap: nowrap;
	}

	&.align-center {
		align-items: center;
	}

	&.justify-center {
		justify-content: center;
	}
	
	&-full {
		display: flex;
		justify-content: center;
		flex-flow: row wrap;
		gap: 24px;

		@include media(">=tablet") {
			.col-4 {
				flex-basis: calc((100% - 24px) / (12/4));
			}
	
			.col-8 {
				flex-basis: calc((100% - 24px) / (12/8));
			}
		}
		&>* {
			flex-grow: 1;
		}
	}

	@include media(">=tablet") {
		display: flex;
		flex-flow: row wrap;
		gap: 24px;
		
		.col-6 {
			flex-basis: calc((100% - 24px) / 2);
		}

		
		
		.col-12 {
			flex-basis: 100%;
		}

		&>* {
			flex-grow: 1;
		}
	}
	
}