@import "include-media";

.schedule,
.speakers,
.hackathon,
.electron {
  text-align: center;
}

.meeting_button {
  color: #31118f;
  font-size: 2rem;
  padding: 8px 30px;
  border-radius: 12px;
  margin-top: 30px;
  margin-left: 40px;
}

.hero {
  margin-top: 110px;
}

.electron {
  padding: 90px 0 70px;
  
  h2 {
    font-size: 2.4rem;
    color: rgb(159, 215, 0);
    font-weight: 900;
    line-height: 1.2;
    text-transform: uppercase;
    margin-bottom: 30px;
  }

  p {
    font-size: 1.6rem;
    color: #777777;
    line-height: 1.5;
    font-weight: 400;
    text-align: justify;
    margin-bottom: 24px;
	margin-top: 24px;
  }

  &_more {
    font-size: 2rem;
  }

  @include media(">=tablet") {
    h2 {
      font-size: 3.1rem;
    }
	p {
		margin-top: 0;
	}
  }

  @include media(">=desktop") {
    p {
      font-size: 1.8rem;
    }
  }
}

.schedule {
  padding: 90px 0 70px;
  color: var(--color-primary);

  &_download {
    font-size: 1.6rem;
    border-radius: 8px;
    background: #31118f;
    color: white;
    padding: 8px 16px;
    width: fit-content;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 20px;
  }

  &_header {
    margin-bottom: 60px;

    h2 {
      font-size: 3rem;
      font-weight: bold;
      margin-bottom: 30px;
      @include media(">=tablet") {
        font-size: 4.9rem;
      }
    }

    p {
      font-size: 1.6rem;
      font-weight: 300;
    }

    h2,
    p {
      line-height: 1.5;
      max-width: 470px;
      margin-left: auto;
      margin-right: auto;
      //   margin: 0 auto 30px auto;
      text-align: center;
    }
  }
}

.partners {
  padding: 90px 0 70px;
  color: var(--color-primary);

  h2 {
    font-size: 3rem;
    margin-bottom: 60px;
    position: relative;
    padding: 10px 0;

    &::before {
      width: 30px;
      content: " ";
      position: absolute;
      border-top: solid 3px #f50136;
      left: 0;
      top: 0;
    }

    @include media(">=tablet") {
      font-size: 4.9rem;
    }
  }

  &_logo {
    display: flex;
    align-items: center;
    flex-basis: 15%;
    max-width: 70px;

    @include media(">=tablet") {
      max-width: 90px;
      flex-basis: 7%;
    }
    & > img {
      // width: 40% !important;
    }
    // border-radius: 20px;
  }

  &_container > div {
    align-items: center;
    column-gap: 12px;
  }
}

.hackathon {
  padding: 90px 0 70px;
  background-color: #31118f;
  color: white;
  line-height: 1.5;

  h2 {
    font-size: 2.2rem;
    margin-bottom: 30px;
    font-weight: bold;
    text-transform: uppercase;
  }

  h3 {
    margin: 60px 0 30px;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 2rem;
  }

  p {
    font-size: 1.6rem;
    text-align: justify;
    margin-bottom: 30px;
  }

  &_info {
    display: flex;
    column-gap: 12px;

    svg {
      margin-top: 8px;
    }

    p {
      display: flex;
      flex-direction: column;

      span {
        font-size: 1.4rem;
      }

      & > span:nth-child(1) {
        font-size: 2rem;
      }
    }
  }

  &_prizes {
    justify-content: space-between;
    max-width: 800px;
    margin: 0 auto 60px;
    padding-top: 30px;
    flex-basis: 90%;
    @include media(">=tablet") {
      padding: 20px;
      margin-bottom: 0;
      padding: 0;
    }
  }

  &_prize {
    display: flex;
    flex-direction: column;
    align-items: center;

    & > span {
      font-size: 1.8rem;
      font-weight: bold;
      text-align: center;
      margin-top: 10px;
      display: inline-block;
      width: 60px;
      height: 85px;
      background-size: cover;
      background-repeat: no-repeat;

      &:nth-child(2) {
        height: auto;
      }

      @include media(">=tablet") {
        width: 106px;
        height: 150px;
        white-space: nowrap;
        font-size: 2.3rem;
      }
    }

    &_p1 {
      background-position: 2% center;
    }

    &_p2 {
      background-position: 50% center;
    }

    &_p3 {
      background-position: 97% center;
    }

    &:nth-child(2) {
      transform: translateY(-40px);
    }
  }

  &_juries {
    display: flex;
    column-gap: 4%;
    row-gap: 30px;
    flex-wrap: wrap;

    @include media(">=tablet") {
      justify-content: flex-start;
      flex-wrap: nowrap;
      column-gap: 2%;
    }
  }

  &_jury {
    display: flex;
    flex-direction: column;
    flex-basis: 48%;

    @include media(">=tablet") {
      flex-basis: 18.4%;
    }

    @include media(">=542px") {
      flex-basis: 30.666%;
    }

    &_overlay {
      overflow: hidden;
      border-radius: 10px;
    }

    &_image {
      border-radius: 10px;
      width: 100%;
      height: 180px;
      background-repeat: no-repeat;
      background-position: center 40%;
      background-size: cover;
      transition: 0.3s;

      &:hover {
        transform: scale(1.1);
      }

      @include media(">=tablet") {
        width: 100%;
        height: 160px;
      }
    }

    svg:hover {
      transition: 0.3s;
      fill: #0077b5;
    }

    p {
      color: #ffcb42;
      margin-bottom: 1rem;
      @include media(">=tablet") {
        font-size: 1.6rem;
      }
    }

    span {
      margin-right: 10px;
      font-size: 1.2rem;
      font-weight: bold;
      letter-spacing: 2px;
    }

    h4 {
      font-size: clamp(1.2rem, 1.2rem + 0.5vw, 1.8rem);
      line-height: 1.5;
      font-weight: bold;
      margin-bottom: 16px;
    }

    &_separator {
      height: 2px;
      width: 80px;
      background-color: #fff;
      margin-bottom: 10px;
    }

    &_social {
      display: flex;
      column-gap: 10px;
      a {
        display: inline-block;
        width: 20px;
        height: 20px;
      }
    }
  }

  @include media(">=tablet") {
    h2 {
      font-size: 3rem;
    }

    p {
      font-size: 1.8rem;
    }
  }
}

.event {
  padding: 90px 0 70px;
  background-color: #31118f;
  color: #ffffff;

  h2 {
    font-size: 2.2rem;
    font-weight: bold;
    margin-bottom: 60px;
  }

  p {
    font-size: clamp(1.2rem, 0.75rem + 0.65vw, 1.6rem);
    line-height: 1.5;
    text-align: justify;
    margin-bottom: 30px;

    @include media(">=tablet") {
      &:first {
        margin-bottom: 0;
      }
    }
  }

  img {
    display: block;
    width: 90%;
    margin: 0 auto;
    border-top-right-radius: 24%;
    border-bottom-left-radius: 24%;
  }

  @include media(">=tablet") {
    h2 {
      font-size: 3rem;
      margin-bottom: 30px;
    }

    img {
      display: inline;
      width: 100%;
    }
  }
}
