@import "include-media";

footer {
	padding-top: 90px;
	background-color: rgba(0, 0, 0, 0.8);
	text-align: center;

	img {
		max-width: 200px !important;
	}

	.link {
		margin: 20px;
		margin-bottom: 8px;
		margin-left: auto;

		@include media("<=460px") {
			display: block;
			width: fit-content;
		}
	}

	.created-by {
		padding: 60px 0 30px;
		color: rgba($color: #fff, $alpha: 0.6);
		font-size: 1.2rem;
		display: flex;
		align-items: center;
		width: fit-content;
		transition: 0.3s;

		a {
			margin-left: 6px;
			padding-bottom: 1px;
			border-bottom: 1px solid #fff;
		}

		&:hover {
			color: rgba($color: #fff, $alpha: 1);
		}
	}
}