@import "include-media";

.wrapper {
	display: block;
	width: 100%;
}

.slide {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 30px;
  width: 100%;
  height: 700px;
  background-blend-mode: overlay;
  background-color: rgba($color: black, $alpha: 0.5);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;

  .separator {
    height: 3px;
    background-color: white;
    width: 120px;
    margin: 58px auto 100px;
  }

  .content {
    color: #fff;
    text-align: center;
    margin: 0 auto;
	height: 100%;
  }

  .title {
    font-size: 2.2rem;
    line-height: 1.4;
    display: inline;
    transition: 0.5s;
    background: linear-gradient(
        to right,
        rgba(100, 200, 200, 1),
        rgba(100, 200, 200, 1)
      ),
      linear-gradient(
        to right,
        rgba(159, 215, 0, 1),
        rgba(159, 215, 0, 1),
        rgba(159, 215, 0, 1)
      );
    background-size: 100% 0.1em, 0 0.1em;
    background-position: 100% 100%, 0 100%;
    background-repeat: no-repeat;
    transition: background-size 400ms;

    &:hover {
      background-size: 0 0.1em, 100% 0.1em;
    }
  }

  .description {
    font-size: 1.6rem;
    line-height: 1.5;
  }

  @include media(">=tablet") {
    padding-left: 100px;
    padding-right: 100px;

    .title {
      font-size: 3rem;
    }

    .description {
      font-size: 2rem;
    }
  }
}
